.popup {
  width: 320px;
  display: none;
}

.popup__title {
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 36px 39px 35px 37px;
}

.popup__description {
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
  margin: 0;
  padding: 0 39px 27px 37px;
}

.popup__wrapper {
  padding: 20px;
  background-color: #f2f2f2;
}

.popup__button {
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  width: 100%;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
  padding: 18px 62px 14px 60px;
  cursor: pointer;
}
